.contenido-main {
  background-color: #ffffff;
  padding: 5rem 2rem;
  margin-top: 0;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-main {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .iconos .lista-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.iconos .lista-iconos li img {
  margin: 0 auto;
  width: 12rem;
}
.iconos .lista-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3rem;
  color: #5e311e;
}

.gallery-pic {
  margin-top: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .gallery-pic {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery-pic .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
  transition: transform 0.3s ease-out;
}
.gallery-pic .imagen img:hover {
  transform: scale(1.1);
}
@media (min-width: 1024px) {
  .gallery-pic .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery-pic .imagen:nth-child(1) img {
    height: 62rem;
  }
  .gallery-pic .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery-pic .imagen:nth-child(2) img {
    width: 62rem;
  }
  .gallery-pic .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery-pic .imagen:nth-child(4) img {
    height: 62rem;
  }
  .gallery-pic .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery-pic .imagen:nth-child(5) img {
    width: 62rem;
  }
  .gallery-pic .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery-pic .imagen:nth-child(6) img {
    width: 62rem;
  }
  .gallery-pic .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery-pic .imagen:nth-child(7) img {
    height: 62rem;
  }
  .gallery-pic .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery-pic .imagen:nth-child(8) img {
    width: 94rem;
  }
  .gallery-pic .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery-pic .imagen:nth-child(9) img {
    width: 62rem;
  }
  .gallery-pic .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery-pic .imagen:nth-child(10) img {
    width: 62rem;
  }
}

.menu-container {
  margin-top: -5rem;
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 96%, 100%;
  background-size: 200%;
  padding: 15rem 5rem 20rem 6rem;
}
@media (min-width: 768px) {
  .menu-container {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
    background-position: center 5rem, center 92%, 100%;
  }
}
@media (min-width: 1024px) {
  .menu-container {
    background-size: 50%, 50%, 100%;
    background-position: center 5rem, center 92%, 100%;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: #ffffff;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu li p {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #c39c63;
}
.menu li .precio {
  font-size: 3.8rem;
  font-weight: 700;
}

.testimoniales-container {
  padding: 8rem 2rem;
}
@media (min-width: 768px) {
  .testimoniales-container {
    padding: 4rem 2rem;
  }
}

.testimonial {
  background-color: #ebe7de;
  padding: 2rem 4rem;
  width: 65%;
  margin: 0 auto;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .testimonial {
    width: 50%;
  }
}
.testimonial .auth {
  text-align: right;
  font-size: 2.4rem;
  color: #5e311e;
  font-family: "Bona Nova", serif;
}

.footer {
  background-color: #5e311e;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #c39c63;
}
.footer-box p,
.footer-box a {
  font-size: 2rem;
  margin: 0;
}

.cr {
  color: #ffffff;
  margin: 0;
  text-align: center;
}

.header {
  padding: 5rem 0;
  position: relative;
  min-height: 70rem;
}
@media (min-width: 480px) {
  .header {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 4rem;
  }
}
.header::before {
  /*'&' tiene encuenta el elemento padre (.header) en este caso*/
  content: "";
  background-color: #000000;
  position: absolute; /*ocupamos declarar position relative en .contenido-header*/
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  display: block;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}

.header-home {
  background-image: url(../img/header_inicio.jpg);
}

.header-aboutus {
  background-image: url(../img/header_nosotros.jpg);
}

.header-process {
  background-image: url(../img/header_proceso.jpg);
}

.header-menu {
  background-image: url(../img/header_menu.jpg);
}

.header-gallery {
  background-image: url(../img/header_galeria.jpg);
}

.header-contact {
  background-image: url(../img/header_contacto.jpg);
}

.nav-home {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .nav-home {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
    justify-content: space-between;
  }
}
.nav-home a {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .nav-home a {
    margin-bottom: 0;
  }
}
.nav-home a:hover {
  color: #c39c63;
}
.nav-home a.active {
  color: #c39c63;
}

.bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
}
.bar .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar .logo {
    width: 20rem;
    margin: 0 auto;
  }
}

