html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
.heading-blanco,
h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem, 0;
  text-transform: uppercase;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5e311e;
}
h2 span, .heading-blanco span {
  font-size: 1.5rem;
  color: #c39c63;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 10rem;
  height: 10rem;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: #ffffff;
}
.heading-blanco span {
  color: #ffffff;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

@media (min-width: 768px) {
  .contenido-aboutus {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.contenido-aboutus .imagen {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contenido-aboutus .imagen {
    margin: 0 2rem 0 0;
  }
}
.contenido-aboutus .imagen img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.contenido-aboutus .texto blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5e311e;
  font-size: 2rem;
  font-weight: 700;
  text-align: left;
}

.process-cafe {
  margin: 8rem;
  gap: 5rem;
  text-align: justify;
  align-items: center;
}
@media (min-width: 768px) {
  .process-cafe {
    display: flex;
    margin: 3rem;
  }
}
.process-cafe .imagen,
.process-cafe .texto {
  flex: 1;
}
.process-cafe:last-of-type {
  margin-bottom: 0;
}
.process-cafe:nth-child(even) .texto {
  order: -1;
}
.process-cafe .imagen {
  transition: transform 0.3s ease-out;
}
.process-cafe .imagen:hover {
  transform: scale(1.1);
}
.process-cafe .texto {
  margin-top: 3rem;
}
.process-cafe .texto h3 {
  margin: 0;
  text-align: left;
  color: #5e311e;
}
.process-cafe .texto h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 0.5rem;
  background-color: #c39c63;
}

.contenido-menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 2/4;
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 16/6;
  }
}
@media (min-width: 768px) {
  .contenido-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contenido-menu .texto p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenido-contact {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.form .campo {
  margin-bottom: 2rem;
}
.form .campo:last-of-type {
  margin-bottom: 0;
}
.form .campo label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.form .campo input:not([type=submit]),
.form .campo select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5e311e;
}
.form .campo input[type=submit] {
  background-color: #5e311e;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #ffffff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.form .campo input[type=submit]:hover {
  cursor: pointer;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.mn-5 {
  margin-top: -5rem;
}

.bg-white {
  background-color: #ffffff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}

/*se recomienda que se compilen hasta lo ultimo*/